$day-width: 100px;
$day-gap: 1px;
$side-paddings: 10px;
$calendar-width: ($day-width * 7) + ($day-gap * 6) + ($side-paddings * 2);
$days:
  mon 0,
  tue ($day-width * 1 + $day-gap * 1),
  wed ($day-width * 2 + $day-gap * 2),
  thu ($day-width * 3 + $day-gap * 3),
  fri ($day-width * 4 + $day-gap * 4),
  sat ($day-width * 5 + $day-gap * 5),
  sun ($day-width * 6 + $day-gap * 6);
